
































import Vue from 'vue';
import BaseCell from '../part/prePart/preBaseCell.vue';
import Pages from './prePage.vue';
import PageDrawer from './pageDrawer.vue';
import { mapGetters, mapActions } from 'vuex';
export default Vue.extend({
    name: 'PreScreen',
    components: {
        BaseCell,
        PageDrawer,
        Pages,
    },
    data() {
        return  {
            screenDomWidth: 0,
            screenDomHeight: 0,
            backgroundImage: 'none',
            backgroundColor: '',
            transform: 'scale(1)',
            marginLeft: '',
            backgroundPosition: '',
            backgroundSize: '100% 100%',
            overflow: 'hidden',
            margin: '0px',
            fullScreenLoading: {},
            loadingDom: false,
        }; 
    },
    computed: {
        ...mapGetters([
            'projectObj',
            'currentPage',
        ]),
        scaleMode(): string {
            return this.projectObj && this.projectObj.scaleMode;
        },
        screenW(): number {
            return this.projectObj && this.projectObj.screenW;
        },
        screenH(): number {
            return this.projectObj && this.projectObj.screenH;
        },
        currentBgColor(): string {
            return this.currentPage && this.currentPage.backgroundColor;
        },
        currentBgImage(): string {
            return this.currentPage && this.currentPage.backgroundImage;
        },
        preScrrenStyle(): any {
            const bgImage = this.currentBgImage || 'none';
            const tf = this.transform;
            return {
                width: `${this.screenW}px`,
                height: `${this.screenH}px`,
                backgroundImage: `url(${bgImage})`,
                backgroundColor: `${this.currentBgColor}`,
                transform: tf,
                backgroundSize: this.backgroundSize,
                marginLeft: this.marginLeft,
                backgroundPosition: this.backgroundPosition,
            };
        },
        dashBoard(): any {
            return {
                backgroundColor: this.backgroundColor,
                overflow: this.overflow,
            };
        },
        preImg(): any {
            const bgImage = this.currentBgImage || 'none';
            return {
                backgroundImage: `url(${bgImage})`,
                backgroundColor: `${this.currentBgColor}`,
                backgroundSize: this.backgroundSize,
                backgroundPosition: this.backgroundPosition,
            }
        },
        marginBlock(): any {
            return {
                margin: this.margin,
            };
        },
        scene(): any {
            const bgImage = this.currentBgImage || 'none';
            const tf = this.transform;
            return {
                width: `${this.screenW}px`,
                height: `${this.screenH}px`,
                backgroundImage: `url(${bgImage})`,
                backgroundColor: `${this.currentBgColor}`,
                transform: tf,
                backgroundSize: '100% 100%',
                backgroundPosition: 'left top',
            };
        }
    },
    methods: {
        ...mapActions([
            'initCoreModel',
        ]),
        /**
         * 编辑器入口方法，调用vuex的初始化模型方法
         */
        initCore() {
            const mode = 'preview';
            this.initCoreModel(mode);
        },
        getPreScreenData() {

        },
        initScreenDom() {
            const domClient = $(window);
            this.screenDomHeight = domClient.height();
            this.screenDomWidth = domClient.width();
        },
        calLayoutProps() {
            // 宽高等比缩放
            if (this.scaleMode === 'equal') {
                const scaleW = this.screenDomWidth / this.screenW;
                const scaleH = this.screenDomHeight / this.screenH;
                this.transform = `scale(${scaleW}, ${scaleH})`;

                this.overflow = 'hidden';
            }
            // 宽度铺满，高度缩放
            if (this.scaleMode === 'HScale') { 

                // 计算出宽度的固定比例
                const scaleW = this.screenDomWidth / this.screenW;
                this.transform = `scale(${scaleW})`;

                // 相对比例下，width的真正宽度
                const realHeigth = this.screenH * scaleW;
                // 垂直间距
                const marginHor = (this.screenDomHeight - realHeigth) / 2;
                if ( marginHor > 0 ) {
                    this.margin = `${marginHor}px 0px`;
                    this.overflow = 'hidden';
                } else {
                    this.overflow = 'hidden auto';
                }
                // 背景图片移动计算
                const bgSizeW = (realHeigth / this.screenDomHeight) * 100;
                this.backgroundPosition = `left ${marginHor}px`;
                this.backgroundSize = `100% ${bgSizeW}%`;
            }
            // 高度铺满，宽度缩放
            if (this.scaleMode === 'WScale') {
                // 计算出高度的固定比例
                const scaleH = this.screenDomHeight / this.screenH;
                this.transform = `scale(${scaleH})`;
                // 相对比例下，width的真正宽度
                const realWidth = this.screenW * scaleH;
                // 水平间距
                const marginVer = (this.screenDomWidth - realWidth) / 2;
                if ( marginVer > 0 ) {
                    this.margin = `0px ${marginVer}px`;
                    this.overflow = 'hidden';
                } else {
                    this.overflow = 'auto hidden';
                }

                // 背景图片移动计算
                const bgSizeW = (realWidth / this.screenDomWidth) * 100;
                this.marginLeft = `${marginVer}px`;
                this.backgroundPosition = `${marginVer}px top`;
                this.backgroundSize = `${bgSizeW}% 100%`;
            }
        },
        /**
         * 监听整个页面的缩放调整
         */
        handleResize() {
            const domClient = $(window);
            this.screenDomHeight = domClient.height();
            this.screenDomWidth = domClient.width();
        },
        openFullScreen() {
            this.loadingDom = true;
            this.fullScreenLoading = this.$loading({
                lock: true,
                text: 'Loading',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.9)'
            });
        },
        closeFullScreen() {
            this.$nextTick(() => {
                setTimeout(() => {
                    this.fullScreenLoading.close();
                    this.loadingDom = false;
                }, 500);
            })
        },
        
    },
    created() {
        // 如果不是打开新的窗口，前面跳转的时候不要销毁数据
        this.openFullScreen();
        this.initCore();
    },
    mounted() {
        this.handleResize();
        this.initScreenDom();
        this.calLayoutProps();
        this.closeFullScreen();
    },
});
