
import Vue from 'vue';
import Cell from './preCell.vue';
import Group from './preGroup.vue';
import { mapState } from 'vuex';

export default Vue.extend({
    components: {
        Cell,
        Group,
    },
    computed: {
        ...mapState({
            pageManage: (state: any) => state.screen.pageManage,
        }),
        // 如果是组件跳转的情况，旧组件销毁在新组件的created之后...
        partList() {
            return this.pageManage.partContainer;
        },
    },
    mounted() {
        console.log(this.partList);
        
    },
    render(h): any {
        const mapNode = (h): any => {
            const conArray = [];
            const hVnode = ( vNodeName, part ): any => {
                return h(vNodeName, {
                    props: {
                        part,
                    },
                    key: part.partId,
                });
            }; 
            for (const item of this.partList) {
                if (item.partType === 'group') {
                    conArray.push( hVnode(Group, item) );
                } else {
                    conArray.push( hVnode(Cell, item) );
                }
            }
            return conArray;
        };
        return h( 'div', {
                attrs: {
                    class: 'group-list',
                    id: "group-list",
                }
            },
            mapNode(h)
        );
    },
})
