












import Vue from 'vue';
import groupCell from './preGroupCell.vue';

export default Vue.extend({
    name: 'group',
    data() {
        return {
            ins: {},
        };
    },
    components: {
        groupCell
    },
    props: {
        part: {
            type: Object,
            default: {
                partId: '',
            },
        },
    },
    computed: {
        partId() {
            return this.part.partId;
        },
        partStyle() {
            return this.part.partStyle;
        },
        pStyle() {
            return {
                width: this.partStyle.width + 'px',
                height: this.partStyle.height + 'px',
                position: this.partStyle.position,
                left: this.partStyle.x + 'px',
                top: this.partStyle.y + 'px',
                transform: `rotate(${this.partStyle.angle}deg)`,
                backgroundColor: this.partStyle.backgroundColor || "",
                zIndex: this.part.layer,
                opacity: 1,
            };
        },
        partGroup() {
            return this.part.partGroup;
        },
        mountedId(): string {
            return 'mounted_' + this.partId
        },
    },
    methods: {
    },
    beforeDestroy() {
    }
});
